
@charset "UTF-8";
.mint-swipe-indicator {
  width: .1rem;
  height: .1rem;
}
.mint-swipe-indicator {
  opacity: .5;
  background: rgba(0, 0, 0, 0.41);
}
.banner_box {
  overflow: hidden;
  position: relative;
  padding-bottom: 50%;
  /*插件布局有问题不适用 需要改写*/
}
.banner_box .mint-swipe {
    position: static;
}
.banner_box .mint-swipe .mint-swipe-items-wrap {
      position: static;
}
.bg_box {
  background: rgba(255, 255, 255, 0);
}
.classify_nav {
  overflow: hidden;
  background: #fff;
  color: #666;
}
.classify_nav .nav_option {
    float: left;
    text-align: center;
    width: 25%;
    padding: .1rem 0;
}
.classify_nav .nav_option .iconfont {
      font-size: .5rem;
}
.classify_nav .nav_option .tit {
      margin: .1rem 0;
      font-size: .24rem;
}
.modular_box .title_box {
  overflow: hidden;
  padding: .15rem 0.2rem;
}
.modular_box .title_box .left .iconfont {
    color: #663300;
    font-size: .4rem;
    margin-right: .1rem;
}
.modular_box .title_box .right {
    margin-top: .05rem;
    float: right;
}
.modular_box .title_box .right .num {
      color: #ffbe4a;
}
.modular_box .title_box .right .iconfont {
      font-size: .24rem;
}
.modular_box .list_box {
  overflow: hidden;
  padding: .1rem .1rem 0 .1rem;
}
.modular_box .list_box .item {
    float: left;
    width: 50%;
    overflow: hidden;
    box-sizing: border-box;
    border-radius: .2rem;
    margin-bottom: .2rem;
    padding: 0 .1rem;
}
.modular_box .list_box .item .pic_box {
      width: 100%;
      height: 2rem;
      position: relative;
      overflow: hidden;
      background: #f2f2f2;
}
.modular_box .list_box .item .pic_box img {
        width: 100%;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
}
.modular_box .list_box .item .pic_box img[lazy=loading] {
        height: 1rem;
        width: 1rem;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
}
.modular_box .list_box .item .info {
      padding: .1rem;
      background: #fff;
      overflow: hidden;
}
.modular_box .list_box .item .info .title {
        font-size: .28rem;
        margin-bottom: .1rem;
        height: .74rem;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
}
.modular_box .list_box .item .info .up_name {
        height: .3rem;
        line-height: 3rem;
        overflow: hidden;
        font-size: .24rem;
        color: #999;
}
.modular_box .list_box .item .info .up_name .up {
          width: 60%;
          float: left;
          line-height: .3rem;
          display: inline-block;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
}
.modular_box .list_box .item .info .up_name .iconfont {
          font-size: .24rem;
          line-height: .3rem;
}
.modular_box .list_box .item .info .up_name .look_num {
          text-align: right;
          display: inline-block;
          width: 40%;
          line-height: .3rem;
          float: left;
          overflow: hidden;
}



